body {
    margin: 0;
    font-family: 'Segoe UI', Tahoma, Geneva, Verdana, sans-serif;
    -webkit-font-smoothing: antialiased;
    -moz-osx-font-smoothing: grayscale;
    background-color: #f8f9fa;
}

.container {
    text-align: center;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.header {
    background-color: #007bff;
    color: white;
    padding: 10px 30px; /* Reduce padding from 15px 40px */
    height: 60px; /* Set a fixed height */
    display: flex;
    align-items: center;
    justify-content: space-between;
    width: 100%;
    box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.1);
}


.logo-container {
    display: flex;
    align-items: center;
    margin-left: 20px;
}

.confirmden-logo {
    width: 220px;
    height: auto;
}

.nav-list {
    list-style-type: none;
    padding: 0;
    display: flex;
    align-items: center;
    gap: 30px;
}

.nav-list li {
    cursor: pointer;
    font-size: 18px;
    font-weight: 500;
    padding: 12px 18px;
    border-radius: 8px;
    transition: background-color 0.3s, color 0.3s, transform 0.2s;
    color: white;
}

.nav-list li:hover {
    background-color: #0056b3;
    color: #ffffff;
    transform: translateY(-2px);
}

.nav-list li.active {
    background-color: #ffffff;
    color: #007bff;
    font-weight: bold;
    box-shadow: 0px 3px 6px rgba(0, 0, 0, 0.1);
}

.account-item {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 18px;
    font-weight: 500;
}

.account-svg {
    width: 26px;
    height: 26px;
    fill: white;
}

.account-item {
    display: flex;
    align-items: center;
    gap: 8px;
    font-size: 18px;
    font-weight: 500;
}


.nav-list li a:hover {
    background-color: #0056b3;
    color: #ffffff;
    transform: translateY(-2px);
}

